<template>
  <div>
    <in-body :mbx="['首页','其他组件','json-Server']">
      <p>依赖：webStorm先运行 json-server</p>
      <p>http://localhost:3003</p>

      <a href="https://www.cnblogs.com/ys-wuhan/p/6387791.html" target="_blank">
        用JSON-server模拟REST API
      </a>
      <a href="http://localhost:3003" target="_blank">
        <Button type="primary">Primary</Button>
      </a>
      <a href="https://www.cnblogs.com/zhouyangla/p/6753673.html" target="_blank">
        vue2.0项目实战（3）使用axios发送请求
      </a>

      <Button type="primary" @click="loadJson">载入数据</Button>
      <div>{{jsonTest}}</div>

      <Divider orientation="left">表格加载json数据</Divider>
      <table-json-s6></table-json-s6>
      <div>test2 函数测试：{{test2}}</div>

    </in-body>
  </div>
</template>

<script>
  import inBody from '../inBody'
  import tableJsonS6 from "../b/tableJsonS6"
  import test2 from '../../utils/test2'
    export default {
      name: "jsonServer"
      ,data:function(){
        return {
          jsonTest:""
          ,test2:test2()
        }
      }
    ,components:{
        inBody
        ,tableJsonS6
      }
      ,methods:{
        loadJson:function(){
            //this.jsonTest = "jjj"
          this.$http.get('/news')
            .then((response) => {
              //console.log(response.data.title);
              console.info("response:",response)
              //debugger
              this.jsonTest = response.data[0].title;
              //console.info(this)
            })
            .catch(function (error) {
              console.log(error);
            });
        }
      }
      ,mounted:function(){
        //组件初始化代码区间
        //this.loadJson()
        //this.test2 = test2.test2Get();
      }


    }
</script>

<style scoped>

</style>



