<template>
  <Table width="800" border :columns="columns2" :data="data3"></Table>
</template>

<script>
    export default {
        name: "tableJsonS6"
      ,data(){
          return {
            columns2: [
              {
                title:"id",
                key:"id",
                width:60,
                align:"center"
              }
                ,{
                  title: 'title',
                  key: 'title'
                }
                ,{
              title:"desc",
                key:"desc",
                width:400
              }
              ,{
              title:"views",
                key:"views",
                width:100,
                align:"center"

              }
            ]
              ,data3:[
                  {
                    name:"kkk"
                  }
              ]
          }
      }
      ,methods:{
          loadTableJson:function(){

            this.$http.get('/news')
              .then((response) => {
                this.data3 = response.data;
              })
              .catch(function (error) {
                console.log(error);
              });


          }
      }
      ,mounted:function(){
        //console.info("tableJsonS6.this:",this)
        //this.data3[0].name = "jjj"
        this.loadTableJson()
      }
    }
</script>

<style scoped>

</style>



